<template>
    <div>
      <slot></slot>
      <div class="personal">
        <img :src="image"/>
        <p>{{name}}</p>
        <p>{{number}}</p>
        <p>{{identity}}</p>
      </div>
    </div>
</template>

<script>
const responseData = {
  image: '../../static/userlogo.jpg',
  name: '陈大虹',
  number: '201430250120',
  identity: '学习委员'
}
export default {
  name: 'personal',
  data: function () {
    return {
      image: responseData.image,
      name: responseData.name,
      number: responseData.number,
      identity: responseData.identity
    }
  }
}
</script>

<style scoped>
.personal {
  text-align: center;
}
img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}
p {
  font-size: 0.47rem;
  font-weight: bold;
  margin: 0.8rem 0;
}
</style>



