<template>
    <div class="discuss-list">
      <p>
        <span>{{topic}}</span><span>{{username}}</span><span>发布</span><span>{{date}}</span>
        <router-link to="discussDetail"><i class="fa fa-chevron-right" aria-hidden="true"></i></router-link>
      </p>
    </div>
</template>

<script>
const responseData = {
  topic: '课设组队',
  username: '黄大宝',
  date: '2017/04/06'
}
export default {
  name: 'discussList',
  data: function () {
    return {
      topic: responseData.topic,
      username: responseData.username,
      date: responseData.date
    }
  }
}
</script>

<style scoped>
a {
    text-decoration: none;
}
.discuss-list{
    width:100%;
    background-color: white;
  }
  .discuss-list p{
    display:block;
    width:90%;
    height:1.5rem;
    line-height:1.5rem;
    border-bottom:0.01rem solid #ccc;
    margin:0.3rem 0.2rem;
    padding:0 0.3rem;
    position: relative;
  }
  .discuss-list p span{
    display:inline-block;
    margin:0rem 0.1rem;
    font-size:0.5rem;
  }
  i {
      font-size: 0.4rem;
      position: absolute;
      top: 0.55rem;
      right: 0.4rem;
  }
  </style>



