<template>
    <div class="message">
      <slot></slot>
      <div class="container">
          <div is="msg-item" v-for="(item, index) in responseDatas" v-bind:msg="item"></div>
          <!--<msg-item v-on:showDetail="isShowDetail"/>-->
          <remind-form />
          <msg-content v-bind:isShow="isShow" v-on:close="isShowDetail"/>
      </div>
    </div>
</template>

<script>
import $ from 'jquery'
import MsgItem from '../components/MsgItem.vue'
import MsgContent from '../components/MsgContent.vue'
import RemindForm from '../components/RemindForm.vue'

export default {
  name: 'message',
  data: function () {
    return {
      isShow: false,
      responseDatas: []
    }
  },
  components: {
    'msg-item': MsgItem,
    'msg-content': MsgContent,
    'remind-form': RemindForm
  },
  methods: {
    isShowDetail: function () {
      this.isShow = !this.isShow
    }
  },
  created: function () {
    var theComponent = this
    $.ajax({
      type: 'GET',
      url: 'http://localhost:3000/apis/user/message',
      success: function (res) {
        console.log(res)
        theComponent.responseDatas = res
        console.log(theComponent.responseDatas)
      }
    })
  }
}
</script>

<style scoped>
.message .container {
  padding: 0.47rem 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
</style>



