<template>
    <div class="homework-addup">
      <span>{{subject}}</span><span>{{title}}</span><span>{{count}}人已交</span><span>删除</span>
      <router-link to="dealHomework"><i class="fa fa-chevron-down" aria-hidden="true"></i></router-link>
    </div>
</template>

<script>
const responseData = {
  subject: '工图',
  title: 'A4Y电子版',
  count: '20'
}
export default {
  name: 'aboutSubmit',
  data: function () {
    return {
      subject: responseData.subject,
      title: responseData.title,
      count: responseData.count
    }
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
}
.homework-addup{
    text-align: left;
    width:100%;
    height:3rem;
    background-color: white;
    display:block;
    height:1.5rem;
    line-height:1.5rem;
    border-bottom:0.01rem solid #ccc;
    margin:0.3rem 0;
    padding:0 0.3rem;
  }
  .homework-addup span{
    display:inline-block;
    margin:0rem 0.2rem;
    font-size:0.5rem;
  }
  .homework-addup span:nth-child(1){
    font-weight:bold;
  }
  .homework-addup span:nth-child(3){
    color:blue;
  }
  .homework-addup span:nth-child(4){
    color:red;
  }
  i {
      position: relative;
      top: -0.1rem;
      left: 0.2rem;
      font-size: 0.4rem;
  }
</style>



