<template>
  <div>
    <slot></slot>
    <div class="container">
        <discuss-item/>
        <rediscuss/>
        <router-link to="discuss"><span class="rtn">返回</span></router-link>
    </div>
  </div>
</template>

<script>
import DiscussItem from '../components/DiscussItem.vue'
import ReDiscuss from '../components/ReDiscuss.vue'

export default {
  name: 'discussDetail',
  components: {
    'discuss-item': DiscussItem,
    'rediscuss': ReDiscuss
  }
}
</script>

<style scoped>
a {
    text-decoration: none;
}
.container {
  padding: 0.47rem 0.47rem;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  text-align: center;
}
.rtn {
    color:#5677fc;
    font-size:0.47rem;
    display:inline-block;
    margin-top: 0.4rem;
}
</style>



