<template>
    <div class="homework-release" v-if="isPublish">
      <form action="">
        <input type="text" placeholder="作业主题">
        <input type="text" placeholder="截止日期">
        <textarea name="homework-text" placeholder="作业内容" cols="20" rows="5"></textarea>
        <button>确认发布</button>
      </form>
    </div>
</template>

<script>
export default {
  name: 'publishHomework',
  props: ['isPublish']
}
</script>
<style scoped>
.homework-release{
    width:100%;
  }
.homework-release form input{
    width:46%;
    height:1rem;
    border:none;
    border:0.01rem solid #999;
    border-radius:0.1rem;
    margin:0.1rem 0.1rem;
    font-size:0.45rem;
    color:#666;
    padding-left: 0.1rem;
  }
  .homework-release form textarea{
    width:90%;
    border:none;
    border:0.01rem solid #999;
    border-radius:0.1rem;
    margin:0.1rem 0;
    padding:0.2rem 0.3rem;
    font-size:0.45rem;
    color:#666;
  }
form button {
  width: 95%;
  height: 1.3rem;
  margin: 0.2rem 0.2rem;
  font-size: 0.4rem;
  border-radius: 0.2rem;
  border: none;
  color: white;
  background-color: #388e3c;
}
</style>



